import { useSelector } from "react-redux"

const Pais = () => {
    
    const pais=useSelector(store=>store.paises.verPais);

    return (
        <main>
            <div className="cont-info">
                <img src={pais.flag} alt="img" />
                <div class="info">
                    <h2>{pais.name}</h2>
                </div>
            </div>
        </main>
    )
}

export default Pais
